<template>
    <div class="song-list-album">
        <div class="album-slide">
            <div class="album-img">
                <img :src="attachImgUrl(tempList.pic)" />
            </div>
            <div class="album-info">
                <h2>: </h2>
                <span>
                    {{tempList.introduction}}
                </span>
            </div>
        </div>
        <div class="album-content">
            <div class="album-title">
                <p>{{tempList.title}}</p>
            </div>
            <div class="album-score"> 
                <div>
                    <h3></h3>
                    <div>
                        <el-rate v-model="average" disabled></el-rate>
                    </div>
                </div>
                <span>{{average*2}}</span>
                <div>
                    <h3></h3>
                    <div @click="setRank">
                        <el-rate v-model="rank" allow-half show-text></el-rate>
                    </div>
                </div>
            </div>
            <div class="songs-body">
                <album-content :songList="listOfSongs">
                    <template slot="title"></template>
                </album-content>
                <comment :playId="songListId" :type="1"></comment>
            </div>
        </div>
    </div>
</template>

<script>
import { mixin } from '../mixins';
import {mapGetters} from 'vuex';
import { listSongDetail,songOfSongId,setRank,getRankOfSongListId } from '../api/index';
import AlbumContent from "../components/AlbumContent.vue";
import Comment from "../components/Comment";

export default {
    name: 'song-list-album',
    mixins: [mixin],
    components:{
        AlbumContent,
        Comment
    },
    data() {
        return {
            songLists: [],      //
            songListId: '',    //id
            average: 0,         //
            rank: 0             //
        }
    },
    computed: {
        ...mapGetters([
            'listOfSongs',      //
            'tempList',         //
            'loginIn',          //
            'userId',           //id
        ])
    },
    created() {
        this.songListId = this.$route.params.id;
        this.getSongId();
        this.getRank(this.songListId);
    },
    methods: {
       // 
       getSongId(){
           listSongDetail(this.songListId)
           .then(res => {
               for(let item of res){
                   this.getSongList(item.songId);
               }
               this.$store.commit('setListOfSongs',this.songLists);
           })
           .catch(err => {
               console.log(err);
           }) 
       },
       //id
       getSongList(id){
           songOfSongId(id)
           .then(res => {
               this.songLists.push(res);
           })
           .catch(err => {
               console.log(err);
           })
       },
       //
       getRank(id){
           getRankOfSongListId(id)
           .then(res => {
               this.average = res/2;
           })
           .catch(err => {
               console.log(err)
           })
       },
       //
       setRank(){
         if(this.loginIn){
             let params = new URLSearchParams();
             params.append('songListId',this.songListId);
             params.append('consumerId',this.userId);
             params.append('score',this.rank*2);
             setRank(params)
             .then(res => {
                 if(res.code == 1){
                     this.notify('','success');
                     this.getRank(this.songListId);
                 }else{
                     this.notify('','error');
                 }
             })
             .catch(err => {
                 this.notify('', 'error');
             })

         }else{
             this.rank = null;
             this.notify('','warning');
         }  
       }

    }
}
</script>

<style lang="scss" scoped>
@import '../assets/css/song-list-album.scss';
</style>


