<template>
    <div class="scroll-top" @click="returnTop">
        <div class="box-in"></div>
    </div>
</template>

<script>
export default {
    name: 'scroll-top',
    methods: {
        returnTop() {
            //
            const upRoll = setInterval(() => {
                const top = document.documentElement.scrollTop ? document.documentElement.scrollTop : document.body.scrollTop;      //
                const speed = top / 20; //
                if(document.documentElement.scrollTop != 0){
                    document.documentElement.scrollTop -= speed;   // 
                    document.body.scrollTop -= speed;
                }else{
                    clearInterval(upRoll);  //
                }
            },8)
        }
    }
}
</script>

<style lang="scss" scoped>
@import '../assets/css/scroll-top.scss';
</style>


