<template>
    <div class="song-list">
        <ul class="song-list-header">
            <li v-for="(item,index) in songStyle" :key="index" @click="handleChangeView(item.name)"
                :class="{active:item.name==activeName}">
                {{item.name}}
            </li>
        </ul>
        <div>
            <content-list :contentList="data"></content-list>
            <div class="pagination">
                <el-pagination @current-change="handleCurrentChange" background layout="total,prev,pager,next"
                    :current-page="currentPage" :page-size="pageSize" :total="albumDatas.length">
                </el-pagination>
            </div>
        </div>
    </div>
</template>

<script>
import {mixin} from '../mixins';
import ContentList from '../components/ContentList';
import { getAllSongList,getSongListOfLikeStyle } from '../api/index';
import {songStyle} from '../assets/data/songList';

export default {
    name: 'song-list',
    mixins: [mixin],
    components:{
        ContentList
    },
    data(){
        return{
            albumDatas: [],      //
            pageSize: 15,       //
            currentPage: 1,     //
            songStyle: [],           //
            activeName: ''          //
        }
    },
    computed:{
        //
        data(){
            return this.albumDatas.slice((this.currentPage - 1)*this.pageSize,this.pageSize*this.currentPage)
        }
    },
    mounted(){
        this.activeName = '';
        this.songStyle = songStyle;
        this.getSongList();
    },
    methods:{
        getSongList(){
            getAllSongList()
            .then(res => {
                this.currentPage = 1;
                this.albumDatas = res
            })
        },
        //
        handleCurrentChange(val){
            this.currentPage = val;
        },
        //style
        handleChangeView(name){
            this.activeName = name;
            this.albumDatas = [];
            if(name == ''){
                this.getSongList();
            }else{
                this.getSongListOfStyle(name);
            }
        },
        //style
        getSongListOfStyle(style){
            getSongListOfLikeStyle(style)
            .then(res => {
                this.currentPage = 1;
                this.albumDatas = res;
            })
        }   
    }
}


</script>

<style lang="scss" scoped>
@import '../assets/css/song-list.scss';
</style> 


