<template>
    <div class="my-music">
        <div class="album-slide">
            <div class="album-img">
                <img :src="attachImgUrl(avator)">
            </div>
             <ul class="album-info">
                 <li>:{{username}}</li>
                 <li>:{{userSex}}</li>
                <li>:{{(birth)}}</li>
                <li>{{location}}</li>
            </ul>
        </div>
        <div class="album-content">
            <div class="album-title">
                {{introduction}}
            </div>
            <div class="songs-body">
                <album-content :songList="collectionList">
                    <template slot="title"></template>
                </album-content>
            </div>
        </div>
    </div>
</template>

<script>
import { mixin } from '../mixins';
import {mapGetters} from 'vuex';
import { getUserOfId,getCollectOfUserId,songOfSongId } from '../api/index';
import AlbumContent from "../components/AlbumContent.vue";

export default {
    name: 'my-music',
    mixins: [mixin],
    components:{
        AlbumContent
    },
    data() {
        return {
            avator: '',            //
            username: '',         //
            userSex: '',          //
            birth: '',            //
            location: '',         //
            introduction: '',     //
            collection: [],       //
            collectionList: [],   //
        }
    },
    computed:{
        ...mapGetters([
            'listOfSongs',      //
            'userId',           //id
        ])
    },
    mounted(){
        this.getMsg(this.userId);
        this.getCollcetion(this.userId);
    },
    methods:{
        getMsg(userId){
            getUserOfId(userId)
            .then(res => {
                this.avator = res.avator;
                this.username = res.username;
                if(res.sex==0){
                    this.userSex = '';
                }else if(res.sex==1){
                    this.userSex = '';
                }
                this.birth = this.attachBirth(res.birth);
                this.location = res.location;
                this.introduction = res.introduction;
                
            })
            .catch(err => {
               console.log(err);
           })
        },
        //
       attachBirth(val){
           return val.substr(0,10);
       },
       //
       getCollcetion(userId){
           getCollectOfUserId(userId)
           .then(res => {
                this.collection = res;
               //id
               for(let item of this.collection){
                   this.getSongsOfIds(item.songId);
               }
           })
           .catch(err => {
               console.log(err);
           })
       },
       //id
       getSongsOfIds(id){
           songOfSongId(id)
           .then(res => {
               this.collectionList.push(res);
           })
           .catch(err => {
               console.log(err);
           })
       }
    }
}
</script>

<style lang="scss" scoped>
@import '../assets/css/my-music.scss'
</style>


