<template>
    <div>
        <div class="comment">
            <h2></h2>
            <div class="comment-msg">
                <div class="comment-img">
                    <img :src="attachImgUrl(avator)" >
                </div>
                <el-input class="comment-input" type="textarea" :rows="2" placeholder="" v-model="textarea">
                </el-input>
            </div>
            <el-button type="primary" class="sub-btn" @click="postComment"></el-button>
        </div>
        <p>{{commentList.length}}</p>
        <hr>
        <ul class="popular" v-for="(item,index) in commentList" :key="index">
            <li>
                <div class="popular-img">
                    <img :src="attachImgUrl(userPic[index])">
                </div>
                <div class="popular-msg">
                    <ul>
                        <li class="name">{{userName[index]}}</li>
                        <li class="time">{{item.createTime}}</li>
                        <li class="content">{{item.content}}</li>
                    </ul>
                </div>
                <div class="up" ref="up" @click="postUp(item.id,item.up,index)">
                    <svg class="icon">
                        <use xlink:href="#icon-zan"></use>
                    </svg>
                    {{item.up}}
                </div>
            </li>
        </ul>  
    </div>
</template>

<script>
import {mixin} from '../mixins';
import {mapGetters} from 'vuex';
import { setComment,setLike,getAllComment, getUserOfId } from '../api/index';
export default {
    name: 'comment',
    mixins: [mixin],
    props: [
        'playId',      //id
        'type'              //0  1
    ],
    computed:{
        ...mapGetters([
            'id',       //id
            'loginIn',  //
            'userId',   //id
            'avator'    //
        ])
    },
    data() {
        return {
            textarea: '',    //
            commentList: [], //
            userPic: [],     //
            userName: [],    //
        }
    },
    mounted(){
        this.getComment();
    },
    methods: {
        //
        postComment(){
            if(this.loginIn){
                let params = new URLSearchParams();
                if(this.type == 0){     //
                    params.append('songId',this.playId);
                }else{
                    params.append('songListId',this.playId);
                }
                params.append('userId',this.userId);
                params.append('type',this.type);
                params.append('content',this.textarea);

                //
                setComment(params)
                .then(res => {
                    if(res.code == 1){
                        this.notify('','success');
                        this.textarea = '';
                        this.getComment();
                    }else{
                        this.notify('','error');
                    }
                })
                .catch(err => {
                    this.notify('', 'error');
                })
            }else{
                this.rank = null;
                this.notify('','warning');
            }
        },
        //
        getComment(){
            getAllComment(this.type,this.playId)
            .then(res => {
                this.commentList = res;
                for (let item of res){
                    this.getUsers(item.userId);
                }
            })
            .catch(err => {
                this.notify('','error');
            })
        },
        //
        getUsers(id){
            getUserOfId(id)
            .then(res => {
                this.userPic.push(res.avator);
                this.userName.push(res.username);
            })
            .catch(err => {
                this.notify('','error');
            })
        },
        //
        postUp(id,up,index){
            if(this.loginIn){
                let params = new URLSearchParams();
                params.append('id',id);
                params.append('up',up+1);
                
                //
                setLike(params)
                .then(res => {
                    if(res.code == 1){
                        this.$refs.up[index].children[0].style.color = '#2796cd';
                        this.getComment();
                    }else{
                        this.notify('','error');
                    }
                })
                .catch(err => {
                    this.notify('', 'error');
                })
            }else{
                this.rank = null;
                this.notify('','warning');
            }
        }

    }
}
</script>

<style lang="scss" scoped>
@import '../assets/css/comment.scss';
</style>


