<template>
    <div>
        <div class="info">
            <div class="title">
                <span></span>
            </div>
            <hr/>
            <div class="personal">
                <el-form :model="registerForm" ref="reigsterForm" label-width="70px" class="demo-ruleForm" :rules="rules">
                    <el-form-item prop="username" label="">
                        <el-input v-model="registerForm.username" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item prop="password" label="">
                        <el-input type="password" v-model="registerForm.password" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item prop="sex" label="">
                        <el-radio-group v-model="registerForm.sex">
                            <el-radio :label="0"></el-radio>
                            <el-radio :label="1"></el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item prop="phoneNum" label="">
                        <el-input v-model="registerForm.phoneNum" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item prop="email" label="">
                        <el-input v-model="registerForm.email" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item prop="birth" label="">
                        <el-date-picker :editable="false" v-model="registerForm.birth" placeholder="" style="width:100%;"></el-date-picker>
                    </el-form-item>
                    <el-form-item prop="introduction" label="">
                        <el-input v-model="registerForm.introduction" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item prop="location" label="">
                        <el-select v-model="registerForm.location" placeholder="" style="width:100%;">
                            <el-option v-for="item in cities" :key="item.value" :label="item.label" :value="item.value"></el-option>
                        </el-select>
                    </el-form-item>
                </el-form>
                <div class="btn">
                    <el-button type="primary" @click="saveMsg"></el-button>
                    <el-button @click="goback(-1)"></el-button>
                </div>
            </div>
            
        </div>
    </div>
</template>

<script>
import {mapGetters} from 'vuex'
import {rules,cities} from '../assets/data/form'
import {mixin} from '../mixins'
import {getUserOfId, updateUserMsg} from '../api/index'

export default {
    name: 'info',
    mixins: [mixin],
    data() {
        return {
            registerForm: {
                username: '',       //
                password: '',       //
                sex: '',            //
                phoneNum: '',       //
                email: '',          //
                birth: '',          //
                introduction: '',   //
                location: ''       //
            },
            cities: [],             //--
            rules: {}               //
        }
    },
    computed:{
        ...mapGetters([
            'userId'
        ])
    },
    created() {
        this.rules = rules;
        this.cities = cities;
    },
    mounted(){
        this.getMsg(this.userId);
    },
    methods: {
        getMsg(userId){
            getUserOfId(userId)
            .then(res => {
                this.registerForm.username = res.username;
                this.registerForm.password = res.password;
                this.registerForm.sex = res.sex;
                this.registerForm.phoneNum = res.phoneNum;
                this.registerForm.email = res.email;
                this.registerForm.birth = new Date(res.birth.slice(0,10));
                this.registerForm.introduction = res.introduction;
                this.registerForm.location = res.location;
            })
            .catch(err => {
                console.log(err);
            })
        },
        saveMsg() {
            let _this = this;
            let d = new Date(this.registerForm.birth);
            let datetime = d.getFullYear() + '-' + (d.getMonth() + 1) + '-' + d.getDate();
            let params = new URLSearchParams();
            params.append('id',this.userId);
            params.append('username',this.registerForm.username);
            params.append('password',this.registerForm.password);
            params.append('sex',this.registerForm.sex);
            params.append('phoneNum',this.registerForm.phoneNum);
            params.append('email',this.registerForm.email);
            params.append('birth',datetime);
            params.append('introduction',this.registerForm.introduction);
            params.append('location',this.registerForm.location);
            updateUserMsg(params)
                .then(res =>{
                   if(res.code == 1){
                       _this.$store.commit('setUsername',this.registerForm.username);
                       _this.notify('','success');  
                       setTimeout(function(){
                           _this.$router.push({path: '/'});
                       },1000);
                   }else{
                       _this.notify('','error');
                   } 
                })
                .catch(err => {
                     _this.notify('','error');
                })
        },
        goback(index){
            this.$router.go(index);
        }
    }
}
</script>

<style lang="scss" scoped>
@import '../assets/css/info.scss'
</style>


